@import "../../variables.scss";

.Toast {
  display: flex;
  align-items: center;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  left: 50%;
  max-width: 50%;
  transform: translateX(-50%);
  background: $BACKGROUND_COLOR;
  border: $BORDER;
  box-shadow: $SHADOW;
  font-weight: bold;
  z-index: 200000000000000000000;
}

.CloseButton {
  color: $DANGER_COLOR;
  margin-left: 10px;
  cursor: pointer;
}
